@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;700&display=swap);
*{
    box-sizing: border-box;
}
body{
    margin: 0;
    color: white;
    background-color: #111;
    font-family: 'Roboto', sans-serif;
}

.lists{
    margin-top: -150px;
}

footer{
    margin-top: 30px;
    margin-bottom: 15px;  
    text-align: center;
}

.loading{
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: black;
    z-index: 99;
    display: flex;
    justify-content: center;
    align-items: center;
    
}
header{
    position: fixed;
    z-index: 999;
    top: 0;
    left: 0;
    right: 0;
    height: 70px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 30px;
    transition: all ease 0.3s;
}

.black{
    background-color: black;
}

.header--logo{
    height: 25px;
}
.header--logo img{
    height: 100%;
}

.header--user{
    height: 35px;
}
.header--user img{
    height: 100%;
}
.featured {
  height: 100vh;
}

.featured--vertical {
  width: inherit;
  height: inherit;
  background: linear-gradient(to top, #111 10%, transparent 90%);
}
.featured--horizontal {
  width: inherit;
  height: inherit;
  background: linear-gradient(to right, #111 30%, transparent 70%);
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding-top: 70px;
  padding-left: 30px;
  padding-bottom: 150px;
}

.featured--name {
  font-size: 60px;
  font-weight: bold;
  line-height: 90%;
}
.featured--info {
  font-size: 18px;
  font-weight: bold;
  margin-top: 15px;
}

.featured--points,
.featured--year,
.featured--seasons {
  display: inline-block;
  margin-right: 15px;
}
.featured--points {
  color: #46d369;
}
.featured--descriptions {
  margin-top: 15px;
  font-size: 20px;
  font-weight: 400;
  color: #999;
  max-width: 45%;
  text-align: justify;
}
.featured--watchbutton,
.featured--mylistbutton {
  display: inline-block;
  font-size: 20px;
  font-weight: bold;
  padding: 10px 25px;
  border-radius: 5px;
  text-decoration: none;
  margin-top: 15px;
  margin-right: 10px;
  opacity: 0.7;
  transition: all ease 0.2s;
}
.featured--watchbutton:hover,
.featured--mylistbutton:hover {
  opacity: 1;
}

.featured--watchbutton {
  color: black;
  background-color: white;
}
.featured--mylistbutton {
  color: white;
  background-color: #333;
}

.featured--genres {
  margin-top: 15px;
  font-size: 20px;
  color: #999;
}

@media (max-width: 760px) {
  .featured--name {
    font-size: 40px;
  }
  .featured--info {
    font-size: 18px;
  }
  .featured--descriptions {
    font-size: 16px;
    max-width: 100%;
    margin-right: 30px;
  }
  .featured--watchbutton,
  .featured--mylistbutton {
    font-size: 18px;
  }
  .featured--genres{
    font-size: 16px;
  }
}

.movieRow {
  margin-bottom: 15px;
}

.movieRow h2 {
  margin: 0;
  margin-left: 30px;
}

.movieRow--listarea {
  overflow-x: hidden;
  padding-left: 30px;
}

.movieRow--item {
  display: inline-block;
  width: 150px;
}

.movieRow--item img {
  width: 100%;
  -webkit-transform: scale(
    0.9
  );
          transform: scale(
    0.9
  ); /* o scale(0.9) é para diminuir a imagem com relação ao tamanho da área da imagem */
  transition: all ease 0.2s;
  cursor: pointer;
}

.movieRow--item img:hover {
  -webkit-transform: scale(1);
          transform: scale(1); /* 100% da área da imagem */
}

.movieRow--arrow--left,
.movieRow--arrow--right {
  position: absolute;
  font-size: 1.5em;
  font-weight: bold;
  width: 40px;
  height: 225px;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 99;
  background-color: rgba(0, 0, 0, 0.6);
  cursor: pointer;
  opacity: 0;
  transition: all ease 0.3s;
}

.movieRow--arrow--left {
  left: 0;
}
.movieRow--arrow--right {
  right: 0;
}

.movieRow:hover .movieRow--arrow--left,
.movieRow:hover .movieRow--arrow--right {
  opacity: 1;
}

@media (max-width: 760px) {
  .movieRow--arrow--left, .movieRow--arrow--right {
    opacity: 1;
  }
}
