.featured {
  height: 100vh;
}

.featured--vertical {
  width: inherit;
  height: inherit;
  background: linear-gradient(to top, #111 10%, transparent 90%);
}
.featured--horizontal {
  width: inherit;
  height: inherit;
  background: linear-gradient(to right, #111 30%, transparent 70%);
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding-top: 70px;
  padding-left: 30px;
  padding-bottom: 150px;
}

.featured--name {
  font-size: 60px;
  font-weight: bold;
  line-height: 90%;
}
.featured--info {
  font-size: 18px;
  font-weight: bold;
  margin-top: 15px;
}

.featured--points,
.featured--year,
.featured--seasons {
  display: inline-block;
  margin-right: 15px;
}
.featured--points {
  color: #46d369;
}
.featured--descriptions {
  margin-top: 15px;
  font-size: 20px;
  font-weight: 400;
  color: #999;
  max-width: 45%;
  text-align: justify;
}
.featured--watchbutton,
.featured--mylistbutton {
  display: inline-block;
  font-size: 20px;
  font-weight: bold;
  padding: 10px 25px;
  border-radius: 5px;
  text-decoration: none;
  margin-top: 15px;
  margin-right: 10px;
  opacity: 0.7;
  transition: all ease 0.2s;
}
.featured--watchbutton:hover,
.featured--mylistbutton:hover {
  opacity: 1;
}

.featured--watchbutton {
  color: black;
  background-color: white;
}
.featured--mylistbutton {
  color: white;
  background-color: #333;
}

.featured--genres {
  margin-top: 15px;
  font-size: 20px;
  color: #999;
}

@media (max-width: 760px) {
  .featured--name {
    font-size: 40px;
  }
  .featured--info {
    font-size: 18px;
  }
  .featured--descriptions {
    font-size: 16px;
    max-width: 100%;
    margin-right: 30px;
  }
  .featured--watchbutton,
  .featured--mylistbutton {
    font-size: 18px;
  }
  .featured--genres{
    font-size: 16px;
  }
}
